<template>
  <b-container fluid class="back-cont">
    <div class="top-bg-cont">
      <img src="../assets/back_top.svg" />
    </div>
    <div class="nav">
      <p v-if="step == 1" class="active">Understand</p>
      <p v-else @click="step = 1" style="cursor: pointer">Understand</p>
      <p v-if="step == 2" class="active">Sketch</p>
      <p v-else @click="step = 2" style="cursor: pointer">Sketch</p>
      <p v-if="step == 3" class="active">Decide</p>
      <p v-else @click="step = 3" style="cursor: pointer">Decide</p>
      <p v-if="step == 4" class="active">Prototype</p>
      <p v-else @click="step = 4" style="cursor: pointer">Prototype</p>
      <p v-if="step == 5" class="active">Test</p>
      <p v-else @click="step = 5" style="cursor: pointer">Test</p>
    </div>
    <div>
      <b-col>
        <transition
          enter-active-class="animate__animated animate__fadeIn animate__faster"
          leave-active-class="animate__animated animate__fadeOut animate__faster"
          mode="out-in"
        >
          <video class="video-box" controls :src="video[step - 1]"></video>
        </transition>
      </b-col>
    </div>
    <div class="ilustrasi">
      <img v-if="step == 1" src="../assets/ilustrasi-1.svg" />
      <img v-if="step == 2" src="../assets/ilustrasi-2.svg" />
      <img v-if="step == 3" src="../assets/ilustrasi-3.svg" />
      <img v-if="step == 4" src="../assets/ilustrasi-4.svg" />
      <img v-if="step == 5" src="../assets/ilustrasi-5.svg" />
    </div>
    <img
      style="width: 100%; margin-top: 87px;"
      v-if="step == 1"
      src="../assets/back_img-1.svg"
    />
    <img
      style="width: 100%; margin-top: 72px;"
      v-if="step == 2"
      src="../assets/back_img-2.svg"
    />
    <img
      style="width: 100%; margin-top: 67px;"
      v-if="step == 3"
      src="../assets/back_img-3.svg"
    />
    <img
      style="width: 100%; margin-top: 40px;"
      v-if="step == 4"
      src="../assets/back_img-4.svg"
    />
    <img
      style="width: 100%; margin-top: 35px;"
      v-if="step == 5"
      src="../assets/back_img-5.svg"
    />
  </b-container>
</template>

<script>
export default {
  name: "SimulationStep",
  data() {
    return {
      video: [],
      step_name: [],
      step: 1,
      max_step: 5,
      api_url: process.env.VUE_APP_API_URL,
    };
  },
  watch: {
    step: function() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },
  created() {
    this.axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/designsprint")
      .then((response) => {
        this.max_step = response.data.length;
        let steps = [];
        let videos = [];
        for (let i = 0; i < this.max_step; i++) {
          steps.push(response.data[i].title);
          videos.push(
            this.api_url + "/images/designsprint/" + response.data[i].video
          );
        }
        this.step_name = steps;
        this.video = videos;
      });
  },
};
</script>

